.quotesCont {
  color: #fff;
  width: 50%;
  margin: 0 auto;
  font-weight: 400;
  font-size: 1.2rem;
}

.loadQuote {
  margin: 0 auto;
  width: 0.85rem;
  height: 0.85rem;
  background: #f4c154;
  box-shadow: 0.85rem 0 #f4c154, -1.25rem 0 #f4c154;
  border-radius: 0.85rem;
  animation: d6 1s infinite linear alternate;
}

@keyframes d6 {
  50% { box-shadow: 12px 0 #f4c154, -12px 0 #f4c154; }
  100% { box-shadow: 24px 0 #f4c154, -12px 0 #f4c154; }
}

.qouteStyle {
  font-style: italic;
}

.navWhite {
  color:black;
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 30px;
}

.navTitle {
  font-size: 30px;
  color: #f4c154;
}

nav div {
  display: flex;
  gap: 20px;
}

nav a {
  color: white;
}

nav a.active {
  font-weight: bold;
  color: #f4c154;
}

nav a:hover {
  text-decoration: underline;
}

.hero-container {
  padding-left: 40px;
  padding-right: 40px;
}

.heroHeader {
  padding-top: 40px;
}

.hero1,
.hero2 {
  padding-top: 40px;
}

.calculatorCont {
  padding-top: 40px;
  display: flex;
  flex-direction: column;
  gap: 60px;
  width: fit-content;
  margin: 0 auto;
}
